import React from "react";
import "./App.css";
import Todo from "./Todo";

const LOCAL_STORAGE_KEY = "todoapp.todo";
class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      items: [],
      currentItem: {
        text: "",
        key: "",
      },
    };
    this.handleInput = this.handleInput.bind(this);
    this.handleDeleteTask = this.handleDeleteTask.bind(this);
    this.addTask = this.addTask.bind(this);
    this.setUpdate = this.setUpdate.bind(this);
  }
  componentDidMount() {
    const storedItems = JSON.parse(localStorage.getItem(LOCAL_STORAGE_KEY));
    if (storedItems) {
      this.setState({
        items: storedItems,
      });
    }
  }
  handleInput(e) {
    this.setState({
      currentItem: {
        text: e.target.value,
        key: Date.now(),
      },
    });
  }

  addTask(e) {
    e.preventDefault();
    const newItem = this.state.currentItem;
    if (newItem.text !== "") {
      const newItems = [...this.state.items, newItem];
      this.setState({
        items: newItems,
        currentItem: {
          text: "",
          key: "",
        },
      });
      localStorage.setItem(LOCAL_STORAGE_KEY, JSON.stringify(newItems));
    }
  }
  handleDeleteTask(key) {
    const filterItems = this.state.items.filter((item) => item.key !== key);
    this.setState({
      items: filterItems,
    });
    localStorage.setItem(LOCAL_STORAGE_KEY, JSON.stringify(filterItems));
  }

  setUpdate(text, key) {
    const items = this.state.items;
    items.map((i) => {
      if (i.key === key) {
        i.text = text;
      }
    });
    this.setState({
      items: items,
    });
  }
  render() {
    return (
      <div className="app">
        <h1>To-Do-App</h1>
        <header>
          <form id="todoForm" onSubmit={this.addTask}>
            <input
              type="text"
              placeholder="Write your todo here.."
              value={this.state.currentItem.text}
              onChange={this.handleInput}
            />
            <button type="submit">Add task</button>
          </form>
        </header>

        <Todo
          items={this.state.items}
          deleteItems={this.handleDeleteTask}
          setUpdate={this.setUpdate}
        />
      </div>
    );
  }
}

export default App;
